body, html {
  height: 100%;
}

body {
  background-color: #222;
  background-image: radial-gradient(circle, rgba(0, 0, 0, 0), rgba(175, 181, 191, .35)), linear-gradient(rgba(0, 0, 0, 0), #d3d8e0);
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
  overflow-y: hidden;
}

.switch {
  width: 150px;
  height: 195px;
  perspective: 700px;
  background-color: #000;
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0 0 10px 2px rgba(0, 0, 0, .2), 0 0 1px 2px #000, inset 0 2px 2px -2px #fff, inset 0 0 2px 15px #47434c, inset 0 0 2px 22px #000;
}

.switch input {
  display: none;
}

.switch input:checked + .button {
  transform: translateZ(20px)rotateX(25deg);
  box-shadow: 0 -10px 20px #ff1818;
}

.switch input:checked + .button .light {
  animation: flicker .2s .3s infinite;
}

.switch input:checked + .button .shine {
  opacity: 1;
}

.switch input:checked + .button .shadow {
  opacity: 0;
}

.switch .button {
  transform-origin: center center -20px;
  transform-style: preserve-3d;
  width: 100%;
  height: 100%;
  cursor: pointer;
  background: linear-gradient(#980000 0%, #6f0000 30%, #6f0000 70%, #980000 100%) no-repeat;
  transition: all .3s cubic-bezier(1, 0, 1, 1);
  position: relative;
  transform: translateZ(20px)rotateX(-25deg);
}

.switch .button:before {
  content: "";
  width: 100%;
  height: 50px;
  transform-origin: top;
  background-color: #b10000;
  background-image: linear-gradient(rgba(255, 255, 255, .8) 10%, rgba(255, 255, 255, .3) 30%, #650000 75%, #320000), none;
  background-position: 50%, 0 0;
  background-repeat: no-repeat;
  background-size: 97% 97%, auto;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  position: absolute;
  top: 0;
  transform: rotateX(-90deg);
}

.switch .button:after {
  content: "";
  width: 100%;
  height: 50px;
  transform-origin: top;
  background-image: linear-gradient(#650000, #320000);
  position: absolute;
  bottom: 0;
  transform: translateY(50px)rotateX(-90deg);
  box-shadow: 0 50px 8px #000, 0 80px 20px rgba(0, 0, 0, .5);
}

.switch .light {
  opacity: 0;
  width: 100%;
  height: 100%;
  background-image: radial-gradient(#ffc97e, #ff1818 40%, rgba(0, 0, 0, 0) 70%);
  animation: light-off 1s;
  position: absolute;
}

.switch .dots {
  width: 100%;
  height: 100%;
  background-image: radial-gradient(rgba(0, 0, 0, 0) 30%, rgba(101, 0, 0, .7) 70%);
  background-size: 10px 10px;
  position: absolute;
}

.switch .characters {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0);
  background-image: linear-gradient(#fff, #fff), radial-gradient(circle, rgba(0, 0, 0, 0) 50%, #fff 52%, #fff 70%, rgba(0, 0, 0, 0) 72%);
  background-position: 50% 20%, 50% 80%;
  background-repeat: no-repeat;
  background-size: 5% 20%, 33% 25%;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  position: absolute;
}

.switch .shine {
  opacity: .3;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0);
  background-image: linear-gradient(#fff, rgba(0, 0, 0, 0) 3%), linear-gradient(rgba(255, 255, 255, .5), rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0) 80%, rgba(255, 255, 255, .5));
  background-position: 50%, 50%;
  background-repeat: no-repeat;
  background-size: 97% 97%, 97% 97%;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  transition: all .3s cubic-bezier(1, 0, 1, 1);
  position: absolute;
}

.switch .shadow {
  opacity: 1;
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0) 70%, rgba(0, 0, 0, .8)) no-repeat;
  transition: all .3s cubic-bezier(1, 0, 1, 1);
  position: absolute;
}

@keyframes flicker {
  0% {
    opacity: 1;
  }

  80% {
    opacity: .8;
  }

  100% {
    opacity: 1;
  }
}

@keyframes light-off {
  0% {
    opacity: 1;
  }

  80% {
    opacity: 0;
  }
}

.link {
  color: gold;
  font-size: 1.5em;
  text-decoration: none;
  transition: color .3s ease-in-out;
  position: absolute;
  top: 6%;
  left: 10%;
}

.link:hover {
  color: #0af;
}

.music {
  position: fixed;
  top: 4%;
  right: 2%;
}

@media (max-width: 480px) {
  .link {
    font-size: 1em;
    top: 2%;
    left: 2%;
  }

  .music {
    top: 2%;
    right: 1%;
  }
}

